import React from "react";
import {formatPrice} from '../helpers';
import PropTypes from "prop-types";
class Item extends React.Component{

    handleClick =() =>{
        this.props.addToOrder(this.props.index);
    }

    static propTypes ={
        details : PropTypes.shape({
            image :PropTypes.string,
            name : PropTypes.string,
            desc : PropTypes.string,
            status :PropTypes.string,
            price    :PropTypes.number
        }),
        addToOrder: PropTypes.func,
    };
render(){
  //  const props = this.props;
    
   // console.log("checkng" , this.props.details.image);
    const image =this.props.details.image;
    const name=this.props.details.name;
    const price=this.props.details.price;
    const desc=this.props.details.desc;
    const status=this.props.details.status; 
    const isAvailable= status==='available';
  

return(
        <li className ="menu">
        <img src={image} alt=""></img>
        <h3 className="item-name">
        {name}
        <span className="price">{formatPrice(price)}</span>
        </h3>
        <p>{desc}</p>
        <button onClick={this.handleClick} disabled={!isAvailable }>{isAvailable ? 'Add to order':'sold out'}</button>
        </li>
        
    )
    }
}


export default Item;