import React from "react";
import {TransitionGroup, CSSTransition} from 'react-transition-group';
import PropTypes from "prop-types";
import {browserHistory} from 'react-router';
import {
    HashRouter,
    Route,
    Link
  } from 'react-router-dom';
  
class Order extends React.Component{

    renderOrder = key =>{
        const item=this.props.items[key];
        const count=this.props.order[key];
        if (!item) return null;
        const isAvailable= item && item.status ==='available';
if(!isAvailable){
     return( 
        <CSSTransition classNames="order" key ={key} timeout ={{enter:500,exit :500}}>
            <li key={key }>Sorry !! {item ?item.name: 'item'} is not available !! </li>
       </CSSTransition>
     )};
return (
<CSSTransition classNames="order" key ={key} timeout ={{enter:500,exit :500}}>
<li key ={key}>
<TransitionGroup component="span" classNames="count">
    <CSSTransition classNames="count" key ={count} timeout ={{enter:500 ,exit:500   }}>
     <span>{count}</span>
     </CSSTransition>
</TransitionGroup>
     {item.name}
<span></span>
   {count* item.price}
   <button onClick={ () => this.props.deleteFromOrder(key)}>&times;</button>
</li>
</CSSTransition>
);
 
    }

    static propTypes ={
        order : PropTypes.object,
        items: PropTypes.object,
        deleteFromOrder : PropTypes.func
    }

    logout = () =>{
      

    }
render(){
    const logout =<button onClick ={this.logout}>Logout</button>
    const orderIds=Object.keys(this.props.order);
    const total=orderIds.reduce((prevTotal,key) => {
        const item=this.props.items[key];
        const count=this.props.order[key];
        const isAvailable=item && item.status==='available';
        if(isAvailable){
            return prevTotal+(count*item.price);
        }
        return prevTotal;   
    },0);

return(
        <div className="Order-wrap">
        <h2>Order</h2>
        <TransitionGroup component="ul" className="order">
            {orderIds.map(this.renderOrder)}
        </TransitionGroup>
<div className="total"><br></br>
Total : <span></span>
    <strong>{total}
    </strong>

    </div> &nbsp;
    
        </div>
        
    )
    }
}
export default Order;