import React from "react";
import AddItem from './AddItem';
import EditForm from './EditForm';
import PropTypes from "prop-types";
class Inventory extends React.Component{

    static propTypes ={
        items : PropTypes.object,
        loadSampleItems :PropTypes.func,
        addItem : PropTypes.func,
        updateItem: PropTypes.func,
        deleteItem:PropTypes.func
    }
render(){
return(
        <div className="Inventory">
       <h2>inventory</h2> 
        {Object.keys(this.props.items).map(key => <EditForm key={key} index={key} item ={ this.props.items[key]} updateItem ={ this.props.updateItem} deleteItem ={this.props.deleteItem}/>)}
       <AddItem addItem ={this.props.addItem}/>
       <button onClick={this.props.loadSampleItems}>Load Sample Items</button>
        </div>
        
    )
    }
}

export default Inventory;