import React from 'react';
import PropTypes from "prop-types";

class  Login extends React.Component {
    
static propTypes ={
    authenticate : PropTypes.func.isRequired
}

    componentWillMount(){
        console.log("kam dhande saare chakde ",this.props.pathname);

    }
    render() {
        let button,header,paragraph;
        
        if(this.props.pathname==="/"){
            button =<button className="facebook" onClick ={() => this.props.authenticate('Facebook')}>
            Log In With Facebook
            </button>   
        }
        else {
            header =<h2>Inventory login </h2>
           paragraph = <p>For Owner Use only</p>;
       button =  <button className="github" onClick ={() => this.props.authenticate('Github')}>Log In With Github</button>
        }
        return (

    <nav className="login">
    {header}
    {paragraph}
            {button}
    </nav>
        )};

        }

export default Login;