import React from "react";
import PropTypes from "prop-types";
class AddItem extends React.Component{

    static propTypes ={
        addItem : PropTypes.func
    }
    nameref= React.createRef();
    priceref= React.createRef();
    statusref= React.createRef();
    descref= React.createRef();
    imageref= React.createRef();
    // creating item after filling out all values
    createItem = (event) => {
        // stop the form from submitting
    event.preventDefault();
    // make an oject 
    const item={
        name:this.nameref.current.value,
        price:parseFloat(this.priceref.current.value),
        status:this.statusref.current.value,
        desc:this.descref.current.value,    
        image:this.imageref.current.value,
    }
  //  console.log(this.nameref.current.value);
  //  console.log(item);
  this.props.addItem(item);

  // reset the form
  event.currentTarget.reset();
}
render(){
return(
        <form className="item-edit" onSubmit={this.createItem}>
            <input name="name" type="text" ref={this.nameref} placeholder="name"/>
            <input name="price" type="text" ref={this.priceref} placeholder="price"/>
            <select name="status" ref={this.statusref}>
        <option value="available">Fresh</option>
        <option value="unavailable">Sold Out</option>

            </select>
            <textarea name="desc" ref={this.descref}type="text" placeholder="desc"></textarea>
            <input name="image" type="text" ref={this.imageref}placeholder="image"/>
            <button type="submit">ADD ITEM</button>
        </form>
        
    );
    }
}

export default AddItem  ;