
import React from 'react';
import Header from './Header';
import Inventory from './Inventory';
import Order from './Order';
import sampleItems from '../sample-items';
import sampleItemsChatStreet from '../sample-items-chatstreet';
import sampleItemsPizza from '../sample-items-pizza';
import Item from './Item';
import Router from './Router';
import base from '../base';
import PropTypes from "prop-types";


import { O_DIRECTORY } from 'constants';
class App extends React.Component{ 

static propTypes ={
        match : PropTypes.object
    }

    state ={
        items:{},
        order:{},
    };
    // life cycle method 
    componentDidMount() {
        console.log("diddddd");
       /* this.ref= base.syncState(`${this.props.match.params.restaurantId}/items`,{
            context:this,
            state:'items'
        });*/
        // load files according to the system
       window.addEventListener('load', this.loadSampleItems);
       // first re load the items from local storage 
       const localStorageRef=localStorage.getItem(this.props.match.params.restaurantId);
       if(localStorageRef){
           this.setState({order: JSON.parse(localStorageRef)});
       }

       //------------
       const   restaurantId = this.props.match.params.restaurantId;
       console.log(restaurantId);
   // load files according to the system
   if("fncseafood" == restaurantId){
       // seafood restaurant menu item loaded in state
   this.setState({items:sampleItems});
      }
           // chatstreet restaurant menu item loaded in state
   
      else if ("chatstreet" == restaurantId){
          this.setState({items:sampleItemsChatStreet});
   
      }
      // pizza restaurant
      else if ("crunchcheesepizza" == restaurantId){
          this.setState({items:sampleItemsPizza});
      }
   
   
    }
    componentWillUnmount(){
        console.log("willmount");

    // base.removeBinding(this.ref);   
    localStorage.removeItem("whoareyou");
   }

componentDidUpdate(){
    console.log(this.state.order, "it updated");
    localStorage.setItem(this.props.match.params.restaurantId,JSON.stringify(this.state.order)); 
}
   

    addItem = item =>{
       console.log("adding items ");
       // take a copy of the latest state first 
        const items ={...this.state.items}
        // add new items to this latest state 
        items[`item${Date.now()}`] = item; // getting date per millisecond assuming user is not adding 2 item per millisecond ; this is to get unique value for all the items added.
        // set the new items object to state
        this.setState({items});
    }

    updateItem = (key , updatedItem) =>{
        // take a copy of current state 
        const items = {... this.state.items };
        // update state 
        items[key ] = updatedItem;
        // set that to state 
        this.setState({items });
    }

    deleteItem = (key ) =>{
        // take a cipy of state 
         const items ={... this.state .items };
         console.log("items",items);
         // update the state or remove an item from state 
       //   items[key] =null;
       delete items[key];
         // update state 
         this.setState({items});
    }
   
    loadSampleItems =() =>{
     //  alert('loading sample items ');
     const   restaurantId = this.props.match.params.restaurantId;
     console.log(restaurantId);
 // load files according to the system
 if("fncseafood" == restaurantId){
     // seafood restaurant menu item loaded in state
this.setState({items:sampleItems});
    }
         // chatstreet restaurant menu item loaded in state

    else if ("chatstreet" == restaurantId){
        this.setState({items:sampleItemsChatStreet});

    }
    // pizza restaurant
    else if ("crunchcheesepizza" == restaurantId){
        this.setState({items:sampleItemsPizza});
    }
}


addToOrder = key    => {
    //take a copy of state
    const order={...this.state.order};
    //either add in order or update the number in order
    order[key]=order[key]+1 ||1;
    //call setstate to update order
    this.setState({ order });
}
deleteFromOrder =(key) =>{
    const order ={... this.state.order};
    delete order[key];
    this.setState({order});
}

    
    render(){
        console.log(this.myinput);
        let showInventory ;
       let myType =localStorage.getItem("whoareyou");
        const   restaurantId = this.props.match.params.restaurantId;
        let header;
        if("fncseafood" == restaurantId){
      header= <Header resname="FNC SEAFOOD" tagline="AWESOME SEAFOOD"/>

        }
        else if ("chatstreet" == restaurantId){
     header= <Header resname="CHAT STREET" tagline="Khata Meetha Khana"/>

        }
        else if ("crunchcheesepizza" == restaurantId){
        header= <Header resname="Crunch Cheese Pizza" tagline="everyday value"/>

        }
        if(myType==="owner"){
            showInventory =                 <Inventory addItem ={this.addItem} updateItem ={this.updateItem} loadSampleItems ={this.loadSampleItems} items ={this.state.items} deleteItem ={this.deleteItem}/>

        }
        
        return(
                <div  className="fast-food-guru">
                <div className="menu">
                {header}
                    <ul className="fishes">
                    {Object.keys(this.state.items).map(key =>
                         <Item key={key} index={key} details={this.state.items[key]} addToOrder={this.addToOrder}> {key}</Item>)}
                    </ul>
                    </div>
                    <Order items={this.state.items} order={this.state.order} deleteFromOrder ={this.deleteFromOrder}/>
               {showInventory}
                </div>
        );

        
    }
}
export default App;